import { PropsWithChildren, forwardRef, useState } from "react"
import { Navigation, Pagination } from "swiper"
import { Swiper, SwiperProps, SwiperRef } from "swiper/react"
import { useDeviceInfo, useDeviceSize } from "@rp/react/web/common/hooks"
import { Box, IconButton, styled } from "@rp/react/web/mui/core"
import { ChevronLeft, ChevronRight } from "@rp/react/web/mui/icons"

const SwiperArrowIconButton = styled(IconButton, { name: "SwiperArrowIconButton" })(
  ({ disabled }: { disabled: boolean }) => ({
    top: "45%",
    zIndex: 1,
    backgroundColor: "rgba(18, 18, 18, 0.38)",
    display: disabled ? "none" : "",
    "&:hover": {
      backgroundColor: "rgba(18, 18, 18, 0.18)",
    },
  }),
)

const SwiperStyled = styled(Swiper, { name: "SwiperStyled" })(() => {
  const { width } = useDeviceSize()
  return {
    "&.swiper": {
      zIndex: 0,
    },
    // Arrows styles
    "& .swiper-wrapper": {
      height: Math.min(454, width) - 76,
    },
    "& .swiper-slide": {
      minWidth: Math.min(454, width),
    },
  }
})

type TrendingCardProps = PropsWithChildren &
  SwiperProps & {
    itemsCount: number
    disablePrev: boolean
    disableNext: boolean
  }

const SwiperWithAbsoluteArrows = forwardRef<SwiperRef, TrendingCardProps>(
  ({ itemsCount, disablePrev, disableNext, children, ...rest }, ref) => {
    const [prevRef, setPrevRef] = useState<HTMLButtonElement | null>(null)
    const [nextRef, setNextRef] = useState<HTMLButtonElement | null>(null)
    const { isMobile, isHoverDevice } = useDeviceInfo()

    return (
      <>
        {!isMobile || isHoverDevice ? (
          <Box
            sx={{
              position: "absolute",
              height: "100%",
              width: "100%",
              pointerEvents: "painted",
            }}
            data-testid="slider-arrow"
          >
            <SwiperArrowIconButton
              ref={setPrevRef}
              className="arrow-btn prev"
              disabled={disablePrev}
              sx={{ left: 8 }}
              size="small"
              data-testid="prev-arrow-button"
            >
              <ChevronLeft />
            </SwiperArrowIconButton>
            <SwiperArrowIconButton
              ref={setNextRef}
              className="arrow-btn next"
              sx={{ float: "right", right: 8 }}
              disabled={disableNext}
              size="small"
              data-testid="next-arrow-button"
            >
              <ChevronRight />
            </SwiperArrowIconButton>
          </Box>
        ) : null}
        <SwiperStyled
          ref={ref}
          modules={[Navigation, Pagination]}
          navigation={{
            enabled: itemsCount - 1 !== 0,
            prevEl: prevRef,
            nextEl: nextRef,
          }}
          {...rest}
        >
          {children}
        </SwiperStyled>
      </>
    )
  },
)

export { SwiperWithAbsoluteArrows }
